import React, {useEffect, useState} from 'react';
import PropTypes from 'prop-types';
import {
    StripeProvider,
    Elements,
} from 'react-stripe-elements';

import CardForm from './../CardForm/index';
import PaymentButton from './../PaymentButton/index';
import Loading from 'views/Loading/v1/Loading';
import Text from 'views/Text/Text/v1/Text';
import Button from 'views/Button/v1/Button';

import {createPaymentIntent, cancelPaymentIntent, cloudCatch} from 'apis/firebase/cloudFunctions';

import SubscriptionHoC from 'views/hoc/Subscribable';
import {getUid} from 'utilities/Uid';
//import {handlePaymentCall} from './../api';
import {sendEvent, log, sendError} from 'utilities/Measure.js';
import {Event} from 'utilities/Create.js';
import {STRIPE_API_KEY} from 'cloudFunctions/dbConfig.js';

const API_KEY = STRIPE_API_KEY;
const FSM_NO_STRIPE = 'nostripe', 
FSM_COLLECT='coll', 
    FSM_SUCCESS="success", FSM_WAIT_FOR_SIGNAL='wfs', //prevents cancelling payment if seperate from loading 
    FSM_LOADING='loading', FSM_CANCELLING = 'cancelling', FSM_CANCELLED = 'cancelled';


const MyStripeProvider = (props) => {
    const [stripe, setStripe] = useState(null);
    const [fsm, setFsm] = useState(FSM_NO_STRIPE);
    const [uid] = useState(getUid());
    const [error, setError] = useState(null);
    const [paymentIntent, setPaymentIntent] = useState({});

    useEffect(()=>{
        if(window.Stripe){
            setStripe(window.Stripe(API_KEY));
            //call payment intents
            onCreatePaymentIntent()
        }else{
            document.querySelector('#stripe-js').addEventListener('load', () => {
                // Create Stripe instance once Stripe.js loads
                setStripe(window.Stripe(API_KEY));
                onCreatePaymentIntent();
            });
        }
        
    }, []);

    const onError = (message) => {
        sendError(`stripe error: ${message}`);
        setError(message);
    }
    const onCreatePaymentIntent = () => {
        setFsm(FSM_WAIT_FOR_SIGNAL);
        sendEvent(new Event('stripe', 'payment-create'));
        props.doSubscribe('stripeCreatePaymentIntent',
            ()=>{return createPaymentIntent(uid, props.paymentType)},
            e => {
                if(e  && e.paymentIntentId){
                    setFsm(FSM_COLLECT);
                    setPaymentIntent(e);
                }else{
                    throw Error(`There was a problem with that payment`);
                }
            },
            cloudCatch,
            onError
        )
    }
    const onPaymentSuccess = () => {
        //setPaymentIntent(null);
        //paymentIntent.fsm = FSM_SUCCESS;
        setFsm(FSM_SUCCESS);
        sendEvent(new Event('stripe', 'payment-success'));
        props.onAccess(true, true);
    }
    const onPaymentError = (message, code) => {
        onError(`Payment failed: ${code}`);
    }
    const handlePayment = (token) => {
        if(token){
            setFsm(FSM_LOADING);
            props.doSubscribe('stripeHandlePayment', 
                ()=>{return stripe.confirmCardPayment(paymentIntent.paymentIntentSecret, 
                    {
                        'payment_method': {
                            card: {token: token.id},
                            billing_details: {
                                name: 'Stimsims ' + props.paymentType
                            }
                        }
                    }
                )
                },
                (e) => {
                    if(e.paymentIntent && e.paymentIntent.status === 'succeeded'){
                        onPaymentSuccess();
                    }else{
                        if(e.error){
                            onPaymentError(e.error.decline_code, e.error.message);
                        }else{
                            onPaymentError('there was a problem, this payment cannot be completed');
                        }
                    }
                },
                cloudCatch,
                onError
            )
        }
    }

    const renderFsm = () => {
        switch(fsm){
             case FSM_CANCELLED: return  <Text tag="p" className="text-center">Payment cancelled</Text>
            case FSM_CANCELLING: return <Loading delay={0} text="Cancelling Payment"/>
            case FSM_SUCCESS: return null;
            case FSM_WAIT_FOR_SIGNAL:
            case FSM_LOADING: return  <div>
                <Loading delay={0} text="Talking to Stripe Payments"/>
            </div>
            case FSM_COLLECT: return <div className="text-center">
                <StripeProvider stripe={stripe}>
                    <div>
                    {/* <Elements>
                    <PaymentButton handlePayment={handlePayment} type={props.paymentType} value={props.paymentValue}/>
                    </Elements> */}
                    <Elements>
                    <CardForm handlePayment={handlePayment} stripe={window.stripe}
                    type={props.paymentType} value={props.paymentValue} />
                    </Elements>
                    </div>
                </StripeProvider>
            </div>
            case FSM_NO_STRIPE:
            default: return <Loading delay={0} text="Waiting for Stripe Payments"/>
        }
    }
    return <div id="stripe-wrapper" style={styleStripe}>
        {error && <Text tag="p" className="text-center">{error}</Text>}
        {!error && renderFsm()}
    </div>
  }

  const styleStripe = {paddingTop: '20px'}

export default SubscriptionHoC(MyStripeProvider);

MyStripeProvider.propTypes = {
    onAccess: PropTypes.func.isRequired,
    eventId: PropTypes.string.isRequired
}