import React, {useState, useEffect, useRef} from 'react';
import styled from 'styled-components'
import SignIn from 'apis/SignIn/SignIn';
import Subscribable from 'views/hoc/Subscribable/index.js';
import {getUrl, getHost, getFullUrl} from 'utilities/Format.js';
import {log} from 'utilities/Measure';

const NestedSignInView = (props) => {
    useEffect(()=>{
        props.recieveSignInProps(props);
    }, [props.isSignedIn])
    return null;
}

const AuthenticatedExamView = Subscribable(SignIn(NestedSignInView, getUrl(), false));

export default AuthenticatedExamView;

