import React, {useEffect, useState} from 'react';
import {PaymentRequestButtonElement, injectStripe} from 'react-stripe-elements';
import Subscribable from 'views/hoc/Subscribable';
import {log, sendError} from 'utilities/Measure.js';
import {STRIPE_API_KEY, PAY_CURRENCY} from 'cloudFunctions/dbConfig.js';
import {cloudCatch} from 'apis/firebase/cloudFunctions';
import Text from 'views/Text/Text/v1/Text';

const PaymentRequestForm = (props) => {
    const [canMakePayment, setCanMakePayment] = useState(true);
    const [paymentRequest, setPaymentRequest] = useState(false);
    const [error, setError] = useState(false);
    useEffect(()=>{
        if(props.stripe && !paymentRequest){
          const paymentRequest = props.stripe.paymentRequest({
            country: 'AU',
            currency: PAY_CURRENCY,
            total: {
              label: `Stripe ${props.type} payment`,
              amount: props.value,
            },
          });
      
          paymentRequest.on('token', ({complete, token, ...data}) => {
            complete('success');
            props.handlePayment(token);
          });
        props.doSubscribe('stripeButtonPayment',
            paymentRequest.canMakePayment,
            (result) => {
              setCanMakePayment(!!result);
            },
            (error) => {
              sendError(`PayementButton canMakePayment error ${error}`);
            },
            cloudCatch,
            onError
          )

          setPaymentRequest(paymentRequest);
        }
    }, [props.stripe])
    const onError = (error) => {
      console.error(`stripe button error`, error);
      sendError(error);
      setError('There was a problem loading the stripe button')
    }
      return <div>
        {error && <Text color="error">{error}</Text>}
        {canMakePayment && paymentRequest &&  <PaymentRequestButtonElement
          paymentRequest={paymentRequest}
          className="PaymentRequestButton"
          style={{
            // For more details on how to style the Payment Request Button, see:
            // https://stripe.com/docs/elements/payment-request-button#styling-the-element
            paymentRequestButton: {
              theme: 'dark',
              height: '48px',
            },
          }}
        />
      }
      </div>
  }
  export default Subscribable(injectStripe(PaymentRequestForm));