import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import {getSubmissionsforCourseWork} from 'apis/firebase/gapiFunctions';
import SubscriptionHoC from 'views/hoc/Subscribable';
import Text from 'views/Text/Text/v1/Text';
import Button from 'views/Button/v1/Button.js';
import Loading from 'views/Loading/v1/Loading';
import {cloudCatch} from 'apis/firebase/cloudFunctions';
import {log} from 'utilities/Measure';
import {AnimatePresence, motion} from 'framer-motion'; 
import {ANIM_LAYOUT_OVERLAP} from 'game/tree/Animations.js';

const SelectSubmission = (props) => {
    const [studentSubmissions, setStudentSubmissions] = useState(null);
    useEffect(()=>{
        handleSelectSubmission()
    }, [])
    const handleSelectSubmission = async (uid) => {
        props.doSubscribe('selectSubmissionGetSubmissionsForCoursework', 
        ()=>{return getSubmissionsforCourseWork(props.courseId, props.courseWorkId)},
        result => {
            if(result.studentSubmissions && result.studentSubmissions.length > 0){
                setStudentSubmissions(result.studentSubmissions.filter(f => f.associatedWithDeveloper));
            }else{
                throw new Error(`no coursework found for course ${props.courseId}`);
            }
        },
        cloudCatch,
        props.onError);
    }
    const handleSelect = (i) => {
       // let i = parseInt(ev.target.name);
        let item = studentSubmissions[i];
        props.onSelect(props.id, {submissionId: item.id, link: item.alternateLink});
    }
    const handleReclaim = (i) => {
        //let i = parseInt(ev.target.name);
        let item = studentSubmissions[i];
        props.onSelect(props.id, {submissionId: item.id, link: item.alternateLink, reclaim: true});
    }
 
    const getSubmissionView = (course, i) => {
        if(course.state === 'CREATED' || course.state === 'NEW'){
            return <React.Fragment>
                <Text className="block text-center">You can turn in this games performance report to complete this assignment.</Text>
                <div>
                <Button id={i} onInput={handleSelect} compact>Turn in</Button>
                </div>
            </React.Fragment>
        }else if(course.state === 'RECLAIMED_BY_STUDENT'){
            return <React.Fragment>
                <Text className="block text-center">You have reclaimed this assignment, 
                you can turn in this games performance report to complete this assignment</Text>
                <div>
                    <Button id={i} onInput={handleSelect} compact>Turn in</Button>
                </div>
            </React.Fragment>
        }else if(course.state === 'TURNED_IN'){
            return <React.Fragment>
            <Text className="block text-center">You have turned in this assignment, 
            you can reclaim and turn in this games performance report in it's place</Text>
            <div>
            <Button id={i} onInput={handleReclaim} compact>Reclaim and turn in</Button>
            </div>
        </React.Fragment>
        }else if(course.state === 'RETURNED'){
            return <React.Fragment>
                <Text className="block text-center">Your teacher has returned this assignment to you, 
                    you can turn in this games performance report in it's place</Text>
                    <div>
                <Button id={i} onInput={handleSelect} compact>Turn in</Button>
                </div>
            </React.Fragment>
        }
    }
    return <AnimatePresence>
        {studentSubmissions && <motion.div key="submissions" {...ANIM_LAYOUT_OVERLAP}>
            {studentSubmissions.map((course, i) => {
                return <div key={course.id} className="text-center">
                    {/* <Text text={[
                        {href: course.alternateLink, text: 'Assignment: ', leave: true},
                        {text: `${course.state}`}
                    ]} />
                    {(course.state === 'CREATED' || course.state === 'RECLAIMED_BY_STUDENT' || course.state === 'NEW') 
                        && <Button id={i} onInput={handleSelect}>Turn in</Button>}
                    {course.state === 'TURNED_IN' && 
                        <Button id={i} onInput={handleReclaim}>reclaim and overwrite</Button>}
                    {course.state === 'RETURNED' && 
                        <Button id={i} onInput={handleSelect}>resubmit</Button>} */}
                    {getSubmissionView(course, i)}
                </div>
            })}
        </motion.div>}
        {!studentSubmissions && <motion.div key="loading" {...ANIM_LAYOUT_OVERLAP}>
            <Loading delay={0} text="Retrieving coursework submissions"/>
            </motion.div>}
    </AnimatePresence>
}

SelectSubmission.propTypes = {
    id: PropTypes.string,
    course: PropTypes.string,
    onSelect: PropTypes.func
}

export default SubscriptionHoC(SelectSubmission);