import React, {useState, useEffect} from 'react';
import {withTheme} from 'styled-components';
import Button from 'views/Button/v1/Button';
import Text from 'views/Text/Text/v1/Text';
import SelectCourse from 'apis/classroom/getters/SelectCourse/index';
import SelectCourseWork from 'apis/classroom/getters/SelectCourseWork/index';
import SelectSubmission from 'apis/classroom/getters/SelectSubmission/index';
import {setGrade, turnIn, reclaimSubmission} from 'apis/firebase/gapiFunctions';
import {studentPermissions} from 'apis/config';
import SubscriptionHoC from 'views/hoc/Subscribable';
import {buildDocument} from 'game/report/utilities';
import { globalHistory } from "@reach/router"
import {cloudCatch} from 'apis/firebase/cloudFunctions';
import {roundTo} from 'utilities/Format';
import {log} from 'utilities/Measure';
import {AnimatePresence, motion} from 'framer-motion'; 
import {ANIM_LAYOUT_OVERLAP} from 'game/tree/Animations.js';
import Loading from 'views/Loading/v1/Loading';
//will need grade to assign
const ST_DEFAULT = 'def', ST_COURSE = 'course', ST_COURSE_WORK = 'coursework', ST_PROCESSING ='processing', 
ST_SUBMISSIONS='submissions', ST_SUCCESS = 'success', ST_ERR = 'err';
const SubmitCourseWork = (props) => {
    const [error, setError] = useState(null);
    const [fsm, setFsm] = useState(ST_COURSE);
    const [courseId, setCourseId] = useState(null);
    const [courseWorkId, setCourseWorkId] = useState(null);
    const [submission, setSubmission] = useState(null);

 
    const changeState = (key, payload) => {
        if(fsm === ST_DEFAULT){ //a call from sign in
            setFsm(ST_COURSE);
        }else if(fsm === ST_COURSE){
            setCourseId(payload.courseId);
            setFsm(ST_COURSE_WORK);
        }else if(fsm === ST_COURSE_WORK){
            setCourseWorkId(payload.courseWorkId);
            setFsm(ST_SUBMISSIONS);
        }else if(fsm === ST_SUBMISSIONS){
            setSubmission(payload);
            setFsm(ST_PROCESSING);
            submitAssignment(courseId, courseWorkId, payload.submissionId, payload.reclaim);
        }else if(fsm === ST_PROCESSING){

        }
    }
    const submitAssignment = (cId, cwId, sId, reclaim) => {
       let fun = reclaim?reclaimSubmission:turnIn;
       setFsm(ST_PROCESSING);
       let document =  buildDocument(props.game, props.data, props.theme, `${props.meta.title} Report`, 
            false, false, globalHistory.location.href);
       props.doSubscribe('submitCourseWorkSubmitAssignment', 
       (q) => { return fun(cId, cwId, sId, props.meta.title, document, roundTo(props.data.finalMark*100, 2))},
       e => {
            setFsm(ST_SUCCESS);
        },
        (e) => {
            if(e.status === 400){
                if(reclaim){
                    return `Failed a precondition: You might be trying to reclaim a submission that is either not turned in, or has been returned.`;
                }else{
                    return `Failed a precondition: You might be trying to turn in an assignment that has already been turned in.`;
                }
                
            }else if( e.error && e.error.message === 'The caller does not have permission'){
                return `Failed a precondition: You are not allowed to edit that item. If you're logged in as a teacher, 
                    please use the teacher section on the game menu to edit student reports.`;
            }else{
                let res = cloudCatch(e);
                return res;
            }
        }, setError)
    }
    const renderState = () => {
        
        switch(fsm){
            case ST_SUCCESS: return <div>
                <div><Text color="primary" className="block text-center">success</Text></div>
                <div><Text className="block text-center"><a href={submission.link} target="_blank">link to submission</a></Text></div>
            </div>
            case ST_PROCESSING: return <Loading text="Submitting your course work"/>
            case ST_SUBMISSIONS: return <SelectSubmission key={ST_SUBMISSIONS} id={ST_SUBMISSIONS} courseId={courseId} courseWorkId={courseWorkId} error={error}
                subscribe={props.subscribe} onSelect={changeState} permissions={studentPermissions} onError={setError} />
            case ST_COURSE_WORK: return  <SelectCourseWork 
                key={ST_COURSE_WORK} id={ST_COURSE_WORK} courseId={courseId} onSelect={changeState} error={error}
                filterByTitle={props.meta.title} pageSize={100}
                subscribe={props.subscribe}  permissions={studentPermissions} onError={setError} />
            case ST_COURSE:  
            default: return <SelectCourse key={ST_COURSE} id={ST_COURSE} onSelect={changeState} permissions={studentPermissions} 
                subscribe={props.subscribe} onError={setError} error={error}/>
        }
    }
    return <AnimatePresence>
        {error && <motion.div key="error" {...ANIM_LAYOUT_OVERLAP}><Text color="secondary">Error: {error}</Text></motion.div>}
        {!error && <motion.div key={fsm} {...ANIM_LAYOUT_OVERLAP}>{renderState()}</motion.div>}
    </AnimatePresence>
}

export default withTheme(SubscriptionHoC(SubmitCourseWork));