import React, {useEffect, useState} from 'react';
import {Helmet} from 'react-helmet';
import Subscribable from 'views/hoc/Subscribable';
import Text from 'views/Text/Text/v1/Text';
import {cloudCatch} from 'apis/firebase/cloudFunctions';
import {log, sendError} from 'utilities/Measure';
import {PAYPAL_SANDBOX_API_KEY, PAYPAL_LIVE_API_KEY, PAY_CURRENCY, PAYPAL_MODE,
  PAY_REPORT_VALUE_PAYPAL, PAY_EXAM_VALUE_PAYPAL} from 'cloudFunctions/dbConfig.js';
  import {capturePaypalPayment} from 'apis/firebase/cloudFunctions';
import LoadScript from './../LoadScript';
import {isDevelopment} from 'utilities/Validation';
import Loading from 'views/Loading/v1/Loading';

  const getClientId = () => {
    let id = PAYPAL_LIVE_API_KEY;
    if(PAYPAL_MODE === 'sandbox') id = PAYPAL_SANDBOX_API_KEY;
    console.log(`paypal mode ${PAYPAL_MODE} client id ${id}`);
    return id;
  }
  //TODO refund wrong values immedietly, handle errors
 
const Paypal = (props) => {
  console.log(`Paypal props`, props);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);
    useEffect(()=>{
        if(window.paypal){
            renderPaypalButton();
        }
    }, []);
    const onError = (err) => {
      console.log(`paypal onError`, err);
      sendError(`paypal error: ${err}`);
      setError(`There was a problem processing that payment.`)
    }
    const getItem = () => {
      if(props.paymentValue === PAY_EXAM_VALUE_PAYPAL){
        return [
          {
            "name": 'exam',
            "quantity": "1",
            "unit_amount":{"currency_code": PAY_CURRENCY, 
            "value": PAY_EXAM_VALUE_PAYPAL
          },
           // "tax": "0",
            "sku": "e2",
            "category": "DIGITAL_GOODS"
          }
        ];
      }else if(props.paymentValue === PAY_REPORT_VALUE_PAYPAL){
        return [
          {
            "name": 'report',
            "quantity": "1",
             "unit_amount": {"currency_code": PAY_CURRENCY, 
             "value": PAY_REPORT_VALUE_PAYPAL
            },
           // "tax": "0",
            "sku": "r1",
            "category": "DIGITAL_GOODS"
          }
        ]
      }else{
        throw Error(`Paypal invalid item error`)
      }
    }
    const renderPaypalButton = () => {
      paypal.Buttons({
        createOrder: function(data, actions) {
          // This function sets up the details of the transaction, including the amount and line item details.
          console.log(`paypal createOrder data`,data);
          setLoading(true);
          let pp = {
            payer: {
              "payment_method": "paypal"
              },
            application_context:{
              // "locale":"fi-FI",
              // "brand_name":"PawelBiernackiSklep",
              // "landing_page":"login",
              "shipping_preference":"NO_SHIPPING",
             // "user_action":"commit"
            },
            purchase_units: [{
              "custom_id": props.user.uid,
              "description": "report description",
              "soft_descriptor": "report soft_descriptor",
            //  "payment_instruction": 'the address is unneccessary',
              amount: {
               // value: '0.01'
                value: props.paymentValue,
                "currency_code": PAY_CURRENCY,
                breakdown: {
                  item_total: {value: props.paymentValue, currency_code: PAY_CURRENCY}
                }
              },
              items: getItem()
            }]
          };
          console.log(`paypal createOrder pp`,pp);
          return actions.order.create(pp);
        },
        onApprove: function(data, actions) {
          console.log(`paypal onApprove data`, data);
          
          return capturePaypalPayment(data.orderID)
          .then(e => {
            console.log(`paypal onApprove then`, e);
            setLoading(false);
            if(e.error){
              throw Error(`Unable to complete payment`);
            }else{
                //throw Error(`test error`) //displayed error message appropriately,
                props.onAccess(true, true);
            }
          })
          .catch(e => {
            setLoading(false);
            onError(e);
          });
        },
        onError: (err) => {
          onError(err);
        }
      }).render('#paypal-button');
    }

    return  <div style={stylesPaypal}>
                <LoadScript 
                  script={`https://www.paypal.com/sdk/js?client-id=${getClientId()}&currency=AUD`}
                  onLoad={renderPaypalButton} 
                />
                {error && <Text color="error">{error}</Text>}
                <div id="paypal-button"></div>
                {loading &&  <Loading delay={0} text="Talking to Paypal payments"/>}
            </div>
}

const stylesPaypal = {borderBox: 'border-box', padding: '20px 0px', maxHeight: '400px', overflow: 'auto'}

export default Subscribable(Paypal);