const firebase = require("firebase");
// Required for side-effects
require("firebase/functions");
import {log, sendError} from 'utilities/Measure.js';

//const COLLECTION_PLAYERS = 'players', COLLECTION_EXAMS = 'exams';
const dbConfig = require('./../../../functions/dbConfig.js');

export const firestoreThen = (callback) => (e) => {
    //delete returns nothing
    log(`firestore inline then e`, e);
    if(!e){
        callback(null);
    }else if(e.exists){
        let r = e.data();
        callback(r);
    }else if(e.data && e.data.success){
        callback(e.data.success);
    }else if(e.error){
        throw new Error(e.error.message);
    }else{
        throw new Error('No such record found');
    }
}
export const firestoreThenInline =  (e) => {
    //e = {error: {message: 'test error'}};
    //delete returns nothing
    log(`firestore inline catch e`, e);
    if(e.exists){
        let r = e.data();
        return r;
    }else if(e.data && e.data.success){
        return e.data.success;
    }else if(e.error){
        throw new Error(e.error.message);
    }else{
        throw new Error('No such record found');
    }
}

export const firestoreCatch = (e) => {
   // log(`firestore catch e`, e);
    if(typeof e === 'string'){
      //  sendError(e);
        return e;
    }else if(e.error){
        //sendError(e.error.message);
        return e.error.message;
    }else{
       // throw new Error('No such record found');
     //  sendError('No such record found');
       return 'No such record found';
    }
}

export const getPlayer = (uid) => {
    return firebase.firestore().collection(dbConfig.COLLECTION_PLAYERS).doc(uid).get()
    .then(firestoreThenInline);
}


export const getExam = (uid, examId) => {
    return firebase.firestore().collection(dbConfig.COLLECTION_PLAYERS).doc(uid)
                                .collection(dbConfig.COLLECTION_EXAMS).doc(examId).get();
}

export const saveExam = (uid, examId, save) => {
    return firebase.firestore().collection(dbConfig.COLLECTION_PLAYERS)
                                .doc(uid)
                                .collection(dbConfig.COLLECTION_EXAMS)
                                .doc(examId).update({
                                    save
                                });
}

export const deleteExam = (uid, examId) => {
    return firebase.firestore()
                    .collection(dbConfig.COLLECTION_PLAYERS)
                    .doc(uid)
                    .collection(dbConfig.COLLECTION_EXAMS)
                    .doc(examId).delete()
                    .then(e => {
                        return firebase.firestore().collection(dbConfig.COLLECTION_PLAYERS).doc(uid).update({
                            [`exams.${examId}`]: firebase.firestore.FieldValue.delete()
                        })
                    })
}
