
import React, {useEffect, useState} from 'react';
import {
    CardElement,
    StripeProvider,
    Elements,
    injectStripe,
  } from 'react-stripe-elements';
import styled from 'styled-components';
import Button from 'views/Button/v1/Button.js';
import Text from 'views/Text/Text/v1/Text';
import {log} from 'utilities/Measure.js';
  

  
  const CARD_ELEMENT_OPTIONS = {
    style: {
      base: {
        fontSize: '18px',
        color: '#424770',
        letterSpacing: '0.025em',
        '::placeholder': {
          color: '#aab7c4',
        },
      },
      invalid: {
        color: '#9e2146',
      },
    },
  };


  //class _CardForm extends React.Component<InjectedProps> {
const CardForm = (props) => {
    const handleSubmit = async (ev) => {
      let token = await props.stripe.createToken({type: 'card', name: 'stimsims'});
      props.handlePayment(token.token);
    };
    return (
        <CardStyles onSubmit={handleSubmit}>
          {props.stripe ? (
            <CardElement
              {...CARD_ELEMENT_OPTIONS}
            />
          ) : (
            <div className="StripeElement loading" />
          )}
          <Button disabled={!props.stripe} onInput={handleSubmit} bgColor={'g3'}>Pay with Card</Button>
        </CardStyles>
      );
  }


export default injectStripe(CardForm);

const CardStyles = styled.form`
  border-top: 2px solid grey;
  border-bottom: 2px solid grey;
`