import React, {useState, useEffect} from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { graphql, Link } from 'gatsby';

import NoScript from 'views/Website/NoScript/NoScript.js';
import Loading from 'views/Loading/v1/Loading.js'
import Loadable from 'react-loadable';
import {sendError} from 'utilities/Measure.js';
import {Helmet} from 'react-helmet';
import Text from 'views/Text/Text/v1/Text';
import ErrorBoundary from 'views/Boundary/Boundary.js';
import SiteContainer from 'views/Container/Container.js';
import {motion} from 'framer-motion';
import {log} from 'utilities/Measure.js';
import Meta from 'views/SEO/Meta/Meta';
import {getUrl, getHost} from 'utilities/Format.js';
import {ANIM_LAYOUT_OVERLAP, ANIM_LAYOUT_OVERLAP_EBE, keys as animKeys, animations} from 'animations';
import {getCharacterSrc} from 'game/Views/Image/poses';
import Main from 'game/Main.js';
import AbsoluteBox from 'views/Box/AbsoluteCentered/index';

const GamePost = (props) => {
    const [Loader, setLoad] = useState(null);
    const [images, setImages] = useState(null);
    console.log(`GamePost props`, props);
    useEffect(()=>{
        setLoad({loadable: Load(Main, props.data.game, props.addMenuItem, props.data.logo, props.data.game.images)});
        let meta = props.data.game;
        getCharacterSrc(meta.images).then(e=> {
          if(meta.gameImages){
              meta.gameImages.map(img => {
                      e[img.id] =img.src.childImageSharp.fluid.src;
              })
          }
          setImages(e);
      }).catch(e => {
              log(`game-post catch`, e);
      })
    }, [])

    const getImg = () => {
      if(props.data.game.twitterCard) return getHost() + props.data.game.twitterCard.childImageSharp.fixed.src;
      return null;
    }
    return (
      
        <SiteContainer style={styles} key="game-post" id="game-post" className="site-container-no-scroll" 

          >
          <Helmet title={`${props.data.game.title} | ${props.data.site.siteMetadata.title} Math Game`} >
   
          </Helmet>
          <Meta {...props.data.game} 
            host={getHost()} //url={getUrl()} 
            tags={props.data.topics} 
            description={props.data.game.snippet}
            img={getImg()} 
            iconAlt={'Stimsims game icon'}
          >
            {props.data.game.descriptions && props.data.game.descriptions.map(d => {
              if(d.text){
                return <p>{d.text}</p>
              }
              return null;
            })}
          </Meta>
          <ErrorBoundary id="GameBoundary" errorView={[
            <Text key={'e0'} tag="p" color="orange" fontSize="remM" >There was a problem running the game.</Text>,
            <Text key={'e1'} tag="p" color="orange" fontSize="remM" >It has been noted and logged.</Text>,
            <Text key={'e2'} tag="p" color="orange" fontSize="remM" >Please try again later.</Text>,
          ]}>
            {Loader && images &&  <Loader.loadable 
              logo={props.data.site.siteMetadata.siteUrl + props.data.logo.childImageSharp.fixed.src}
              images={images}
              BUILD_GAME={props.BUILD_GAME}
              />}
            {!Loader && props.addMenuItem && <Loading addMenuItem={props.addMenuItem} />}
          </ErrorBoundary>

        </SiteContainer>
        
    )
}

const styles = {height:'100%', position: 'relative'};
const motionStyles = {position: 'absolute', width: '100%', top: '50%', transform: 'translateY(-50%)'}
GamePost.propTypes = {
    data: PropTypes.shape({
      game: PropTypes.shape({
          import: PropTypes.string,
          title: PropTypes.string,
      }),
      site: PropTypes.object,
      logo: PropTypes.object,
  }),
}
export default GamePost;

const Load = (Main, game, addMenuItem, logo, images) => {
  return Loadable.Map({
    timeout: 10000,
    delay: 0,
    loader: {
      game: ()=>{return getGame(game.import)},
     // GameTool: () => import('game/Main.js'),
    },
    loading: (props)=>{
      if (props.error) {
        sendError(`failed to load game main page: ${props.error.message} stack ${props.error.stack}`, true, 1);
        return <motion.div key="game-post-error"  style={motionStyles} 
        // {...animations[animKeys.ANIM_LAYOUT_OVERLAP_EBE]}
        {...ANIM_LAYOUT_OVERLAP}
        >
          <AbsoluteBox>
          <Text  key={'game-post-loading-text'} id={'game-post-loading-text'} tag="p" className="text-center">
           Error loading game. Some browsers do not have the features this game requires. 
          Please use an up to date browser for best performance.
          </Text>
        <div className="text-center"><a href="/mathgames/">return to games page</a></div>
          </AbsoluteBox>
        </motion.div>;
      } else {
        return <motion.div key="game-post-loading" id={'game-post-loading'}  style={motionStyles} 
        // {...animations[animKeys.ANIM_LAYOUT_OVERLAP_EBE]}
        {...ANIM_LAYOUT_OVERLAP}
        >
         <AbsoluteBox>
         <Loading key={'game-post-loading-view'} id={'game-post-loading-view'} name="game-post" length="50px" />
         </AbsoluteBox>
          </motion.div>;
        //return <p>Hi</p>
      }
      
    },
    render(loaded, props) {
      // let GameTool = loaded.GameTool.default;
        // return <GameTool 
        //   {...props}  
        //   game={loaded.game.default} 
        //   meta={game} 
        //   addMenuItem={addMenuItem} 
        //   audio={game.audio} 
        // />;
        return <Main 
            {...props}  
            game={loaded.game.default} 
            meta={game} 
            addMenuItem={addMenuItem} 
            audio={game.audio} 
            BUILD_GAME={props.BUILD_GAME}
        />
      //return <p style={{color: 'cyan'}}>Game Post</p>
    }
  });
}

const getGame = (game) => {
  switch(game){
    case 'pythagoras':return import('games/Pythagoras/index.js');
    case 'lovecraft-loop':return import('games/LovecraftLoop/index.js');
    case 'testtool':return import('games/TestTool/index.js');
    case 'fairy-toxic-waste-removal-fraction-addition':return import('games/CleanWork/index.js');
    case 'fairy-toxic-waste-removal-fraction-addition-excerpt':return import('games/FractionsExperiment/index.js');
    default:
      throw new SyntaxError('No game found');
  }
}





export const pageQuery = graphql`
  query GamePostByID($import: String!) {
    site {
      siteMetadata {
        ...siteDataFragment
      }
    },
    logo: file(relativePath: { regex: "/favicon/" }) {
      childImageSharp{
        fixed(width: 512, height: 512) {
            ...GatsbyImageSharpFixed
        }
      }
    },
    game: gamesMetaJson(import: { eq: $import }) {
      id
      path
      date
      import
      complete
      hasDifficultySetting
      title
      topics
      snippet
      feedback
      credits{
          heading
          items {
            text
            href
          }
      }
      measurments 
      descriptions {
        id
        text
        img {
          childImageSharp {
            fixed(width: 500) {
              ...GatsbyImageSharpFixed
            }
          }
        }
      }
      tweet
      type
      analytics
      reports
      form {
        type
        text
        options
        id
        value
      }
      sounds
      document{
        id
        text
        tag
        heading
        subheading
      }
      audio
      images
      gameSettings{
        visible
        key
        values
      }
      gameImages{
        id
        src {
          childImageSharp {
            fluid(maxWidth: 800) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
      intro{
        img{
          key
          pose
        }
        text{
          text
          toLatex
          color
        }
      }
      twitterCard{
        childImageSharp {
          ...socialCardFragment
        }
      }
    }
  }
`

